.text_message {
  width: 100%;
  border-radius: 4px;
  padding: 3px 5px;
  line-height: 20px;
  font-size: 15px;
  height: 180px;
  resize: none;
  box-sizing: border-box;
  color: #222;
}

.wrapper {
  max-width: 560px;
  width: 90vw;
  background-color: #fbfbfb;
  padding: 18px;
  box-sizing: border-box;
  border-radius: 7px;
  border: 1px solid rgb(193, 193, 193);
}

.cbox_wrapper {
  display: flex;
  justify-content: flex-end;
  color: #333;
  font-size: 14px;
}

.nav {
  display: flex;
  margin-bottom: 18px;
}

.nav_item {
  /* color: #aaa; */
}

.nav_item + .nav_item {
  margin-left: 9px;
}

.btn_wrapper {
  margin-top: 18px;
  display: flex;
  justify-content: flex-end;
}

.id_label_wrapper, .event_label_wrapper {
  display: flex;
  flex-direction: column;
  color: #333;
  font-size: 15px;
}

.marginTop {
  margin-top: 9px;
}

.marginTop_2 {
  margin-top: 18px;
}