.groove_shadow {
  border: 1px solid rgb(193, 193, 193);
  background-color: rgb(255, 255, 255);
  box-shadow: inset 0 3px rgb(235, 235, 235), 0 0 rgb(235, 235, 235);

  width: 100%;
  border-radius: 4px;
  padding: 3px 5px;
  line-height: 20px;
  height: 33px;
  box-sizing: border-box;
}

.small {
  width: 160px;
}

.normal {
  width: 210px;
}

.big {
  width: 310px;
}